//  id 
export const sampleMessages = Array.from({ length: 10 }, (_, index) => {
    const id = index + 1; // id  1 
    const username = `${id}`; //  "1" 
    const messages = [
        { text: '', time: '10:00', isMe: false },
        { text: '', time: '10:01', isMe: true },
        { text: '', time: '10:02', isMe: false },
        { text: '', time: '10:03', isMe: true },
    ];
    const latestMessage = messages[messages.length - 1].text; // 
    const time = messages[messages.length - 1].time; // 

    return {
        userId: id.toString(),
        username,
        avatar: '/static/default/logo.png',
        latestMessage,
        time,
        messages, // 
    };
});